
export default {
	onLaunch: function() {
		console.log('App Launch')



		// 部分配置需要从服务器加载
		this.$Request.post(this.$api.common.config).then((res) => {
			if (res.code == 200) {
				this.$SysCache.put('web_config', res.data)
			}
		});































	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
